.gridGames {
    display: grid;
    grid-template-columns: auto auto auto;
    position: absolute;
    top: 30%;
    left: 30%;
    z-index: 200;
  }

  .gridGames-item {
    padding: 20px;
    width: 150px;
    height: 180px;
    vertical-align: middle;
  }

  .gridGames > * {
    background: rgba(0,0,0,0.1);
    border: 1px white solid;
  }



  @import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
  
    .buttonPlay span {
        line-height: 100px;
        font-family: "Montserrat", sans-serif;
        font-size: 10px;
        font-weight: semibold;
        top: -50%;
        left: 40%;
        position: absolute;
      }

    .buttonPlay {
      width: 50%;
      height: 30%;
      background: #f3f0f1;
      background: #f3f0f1;
      margin-bottom: 25px;
      border-radius: 32px;
      text-align: center;
      cursor: pointer;
      transition: all 0.1s ease-in-out;
      z-index: 10000000;
    }

    .buttonPlay:nth-child(1) {
      box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
        6px 6px 10px rgba(0, 0, 0, 0.2);
      color: #6f6cde;
      
    }

    .buttonPlay:nth-child(1):hover {
      opacity: 0.3;
      box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
        6px 6px 10px rgba(0, 0, 0, 0.2);
    }

    .buttonPlay:nth-child(1):active {
      opacity: 1;
      box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
        inset 8px 8px 16px rgba(0, 0, 0, 0.1);
      color: #000000;
    }

    .buttonPlay:nth-child(2) {
      opacity: 0.3;
      box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
        6px 6px 10px rgba(0, 0, 0, 0.2);
      color: #6f6cde;
    }
    .buttonPlay:nth-child(3) {
      box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
        inset 8px 8px 16px rgba(0, 0, 0, 0.1);
      color: #000000;
      
    }

    .buttonPlay:nth-child(3):hover {
      opacity: 1;
      box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
        6px 6px 10px rgba(0, 0, 0, 0.2);
    }
  
    .playSessionPosition{
      right: 72vw;
      bottom: 5vh;
      position: absolute;
    }

    .playSessionPosition span{
      position: relative;
      left: 0%;
      top: -125%;
    }


    .betPosition{
      left: 25%;
      position: relative;
    }

    .betMenu-item {
      padding: 10px 0;
      width: 100px;
      height: 100px;
      vertical-align: middle;
    }

    .betMenu-item label{
      font-size: min(max(30px, 2vmin),200px);
      color: #fff;
    }

    .betMenu-item select{
      padding: 1px;
    }

    .unityViewport{
      visibility: visible;
      position: relative;
      height: 40vmin !important;
      min-height: 500px !important;
      width: 52vmin !important;
      min-width: 600px !important;
      
    }

    .unityBackground{
      visibility: hidden;
      position: relative;
    }

    #unityLocation{
      position: absolute;
      top: 15vh;
      left: 27vw;
    }

    .betMenu-item-label{
      position: absolute;
      top:21%;
      left: 0;
      padding: 10px 0;
      font-size: 16px;
      color: #fff;
      pointer-events: none;
    }

    .betMenu-item-select{
      width: 100%;
      padding: 10px 0;
      font-size: 16px;
      color: lightgray;
      margin-bottom: 30px;
      border: none;
      border-bottom: 1px solid #fff;
      outline: none;
      background: transparent;
    }

    .betState{
      pointer-events: none;
      opacity: 0.4;
    }

    .accountMoneyBetPosition{
      position: absolute;
      bottom: 20%;
      right: 48vw;
      font-size: 5vmin;
    }

    .betInfoVisibility{
      visibility: hidden;
    }

    .betInfoPosition{
      position: absolute;
      bottom: 10%;
      left: 39%;
    }

    .accountInfoWinInfo{
      font-size: 50%;
      position: relative;
      left: -5%;
      text-align: left;
    }

    .accountInfoWinGames{
      font-size:  min(max(1.25vmin, 10px), 200px);
      position: relative;
      right: -25%;
      text-align: right;
      top: -10%;
      overflow: auto;
      height: 80%;
      width: 84%;
    }

    .accountInfoWinGames input{
      position: relative;
      top: 2px;
      border-radius: 25%;
      transition: 0.8s;
    }

    .accountInfoWinGames input:hover{
      background-color: lightblue;
    }

    ::-webkit-scrollbar {
      width: 10px;
    }

    ::-webkit-scrollbar-track{
      border: 7px solid #232943;
      box-shadow: inset 0 0 2.5px 2px rgba(0,0,0,0.5);
      margin-bottom: 10px;
      margin-top: 30px; 
      border-radius: 8px;
    }

    ::-webkit-scrollbar-thumb{
      background:linear-gradient(
        45deg,
        #c7c7c7,
        #ffffff
      );
      border-radius: 8px;
    }

    .accountInfoWinGames table, th, td{
      padding: 10px;
    } 

    footer {
      align-items: center;
      bottom: 0;
      color: #666666;
      display: flex;
      font-size: 0.75em;
      height: 50px;
      justify-content: center;
      position: fixed;
      text-transform: uppercase;
      width: 50px;
      z-index: 1;
      right: 50%;
      bottom: 10%;
      cursor: pointer;
    }