#mainHeaderTitle{
    position: absolute;
    top: -1%;
    left: 2.5%;
}

#userIntialInput{
    position: fixed;
    display: inline;
    top: 4%;
    right: 1vmin;
}

#userLogin{
    position: relative;
    right: 0;
}

#userSignIn{
    position: relative;
    right: 0;
}

#userTag{
    position: relative;
    right: 0;
    display : inline-block;
    padding:0.3em 1.2em;
    margin:0 0.3em 0.3em 0;
    font-family:'Roboto',sans-serif;
    font-family:'Roboto',sans-serif;
    font-weight: 1000;
    font-size: min(max(20px, 2vmin), 200px);
    color:rgb(99, 99, 99);
    
}

.mainButton{
    padding:0.3em 1.2em;
    margin:0 0.3em 0.3em 0;
    border-radius: 2em;
    text-decoration:none;
    box-sizing: border-box;
    font-family:'Roboto',sans-serif;
    font-weight: 700;
    color:rgb(99, 99, 99);
    background-color:#d3d3d3;
    text-align:center;
    transition: all 0.2s;
    height: 12vmin;
    max-height: 70px;
    width: 25vmin;
    max-width: 100px;
    font-size: min(max(16px, 1vmin), 200px);
}

.mainButton:hover{
    background-color:#4d4d4d;
    transition-duration: 0.5 ;
}

.logIntoService{
    width: 14em;
    min-width: 100px !important;
    height: 25vh;
    min-height: 250px;
    border: 5px solid rgb(99, 99, 99);
    padding: 50px;
    margin: auto ; 
    border-radius: 2em;
    background-color: #2f343d;
    display: inline-block;
    
}

#aboveWindow{
    position: absolute;
    left: 25%;
    right:25%;
    z-index: 1000;
}

.inputLog{
    position: relative;
}

.inputLog input{
    width: 100%;
    height: 1vh;
    padding: 10px 0;
    font-size: min(50px,max(12px, 3vmin));
    color: #fff;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    background: transparent;
}

.inputLog label{
    position: absolute;
    top:0;
    left: 0;
    padding: 10px 0;
    font-size: 2vmin;
    color: #fff;
    pointer-events: none;
    transition: .5s;
  }

.inputLog input:focus ~ label,
.inputLog input:valid ~ label {
  top: -1.7em;
  left: 0;
  font-size: min(100px, max(20px, 1.2vmin));
  font-weight: 500;
}

.title{
    font-weight: bold;
    color: #fff;
    position: relative;
    top: -1.2em;
}

#loginProcess{
    position: relative;
    bottom: -45px;
}

#signInProcess{
    position: relative;
    bottom: 0vmin;
}

.close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
    color: rgb(51, 51, 51);
  }

  .close:hover {
    opacity: 1;
  }

  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: rgb(7, 7, 7);
  }

  .close:before {
    transform: rotate(45deg);
  }
  
  .close:after {
    transform: rotate(-45deg);
  }

.alert{
    background-image: linear-gradient(rgba(0,0,0,.25), rgba(0,0,0,0));
    border-radius: 8px;
    border: 1px solid rgba(0,0,0,0.1);
    box-shadow: inset 0 0 0 7px rgba(255,255,255,.5);
    position: absolute;
    height: 10%;
    width: 30%;
    left: 35%;
    bottom: 5%;
    margin: auto;
    vertical-align: middle;
    text-align: center;
    z-index: 1500;
}

.alert-message-color{
    background-color: rgb(139, 139, 139);
}

.alert-warning-color{
    background-color: rgb(180, 139, 139);
}

.logo{
    position: absolute;
    left: 20%;
    z-index: 100;
    opacity: 0.3;
    filter: grayscale(100%);
    pointer-events:none;
}

.serviceBox{
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    padding: 15px 15px;
    margin: 0 auto;
    position: absolute;
    right: 25%;
    left: 25%;
    top: 2vmin;
    z-index: 1;
    overflow: auto;
    width: 20em;
    height: 30em;
    background: linear-gradient(-135deg,#797979,#303030);
    border-radius: 8px;
    font-size: 20px;
}

.serviceBox img{
    width: 4em;
    min-width: 100px; 
    height: 5em;
    min-height: 150px;
    display: block;
}

.closeFixed{
    position: fixed;
    top: 35%;
    right: 36.5%;

}

.account-balance-ingame{
    position: absolute;
    left: 30vw;
    bottom: -7vw;
    display: inline-block;
    font-size: min(max(20px,4vmin),200px);
}
.account-balance{
    position: fixed;
    right: 35%;
    top: 8%;
    display: inline-block;
    font-size: min(max(16px,4vmin),200px) !important;
}

.unity-loading{
    position: fixed;
