.base-timer {
    position: absolute;
    height: 25vmin;
    width: 25vmin;
  }

  .base-timer__circle {
    fill: none;
    stroke: none;
  }

  .base-timer__path-elapsed {
    stroke-width: 7px;
    stroke: grey;
  }

  .timer-position{
      position: absolute;
      bottom: 70vmin;
      right: 25vmin;
  }

  .base-timer span{
        position: absolute;
        width: 10%;
        height: 10%;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 13vmin;
  }

  .base-timer__path-remaining {
    stroke-width: 7px;
  
    stroke-linecap: round;

    transform: rotate(90deg);
    transform-origin: center;
  
    transition: 1s linear all;
  
    stroke: currentColor;
  }
  
  .base-timer__svg {
    transform: scaleX(-1);
  }

  .timeleftLocation{
    position: absolute;
    right: 45%;
    top: 50%;
    transform: translateY(450%);
  }